import React from 'react'
export default ({
	newName,
	newNumber,
	handleNameChange,
	handleNumberChange,
	handleSubmit
}) => {
	return (
		<form onSubmit={handleSubmit}>
			<div>
				name: <input value={newName} onChange={handleNameChange} />
			</div>
			<div>
				number:{' '}
				<input value={newNumber} onChange={handleNumberChange} />
			</div>
			<div>
				<button type="submit">add</button>
			</div>
		</form>
	)
}
